.editable-cell {
    position: relative;
}

.editable-cell-value-wrap {
    padding: 5px 0px;
    cursor: pointer;
}

.editable-row:hover .editable-cell-value-wrap {
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    padding: 4px 6px;
}

.tree-parent-div{
    display:none;
    cursor: pointer;
}

.tree-span{
    color:#4997f1;
    margin-left:5px;
}

.tree-title:hover .tree-parent-div{
    display:inline-block;
}

#components-table-demo-drag-sorting tr.drop-over-downward td {
    border-bottom: 2px dashed #1890ff;
}

#components-table-demo-drag-sorting tr.drop-over-upward td {
    border-top: 2px dashed #1890ff;
}

.vLine {
    border-left: solid 2px #ccc;
}
.ant-modal-footer{
    margin-top: 26px;
    padding: 8px 16px;
    text-align: right;
    background: transparent;
    border-top: 1px solid #e8e8e8;
    border-radius: 0 0 4px 4px;
}

.ant-layout-sider {
    position: relative;
    min-width: 0;
    background: #f0f2f5;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}
.anticon {
    display: inline-block;
    color: #716868;
    font-style: normal;
    line-height: 0;
    text-align: center;
    text-transform: none;
    vertical-align: -0.125em;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.ant-layout-sider-trigger {
    position: fixed;
    bottom: 0;
    z-index: 1;
    height: 48px;
    color: #fff;
    line-height: 48px;
    text-align: center;
    background: #f0f2f5;
    cursor: pointer;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}
